




interface Imessage {
  type?: string;
  message?: string;
}
import {Vue, Component, Prop } from 'vue-property-decorator';
import {Message} from 'element-ui';
@Component({})
export default class ZhMessage extends Vue {
    @Prop({default: {}}) message: any;
    @Prop({default: false}) showMessage: any;
    private selfMessage: Imessage = this.message;
    created() {
        this.tip();
    }
    mounted() {
      setTimeout(() => {
              this.$emit('close');
            })
    }
    tip() {
          const message: any = this.message;
          Message({
                  type: message.type,
                  message: message.message
          });
          
      }
}
