




















interface Itips {
    content?: string;
    title?: string;
    comfirm?: string;
    cancel?: string;
}
import {Vue, Component, Prop} from 'vue-property-decorator';
@Component({})
export default class SharedTipsModal extends Vue {
    @Prop({default: ''}) content: Itips = {};
    @Prop({default: false}) visible = false;


    get _openModal() {
      return this.visible;
    }

    set _openModal(val) {
       val ? "" : this.closeModal();
    }
    
    closeModal() {
        this.$emit('closeModal');
    }

    comfirm() {
        this.$emit('comfirm');
    }

}
