








import {Vue, Component, Prop} from 'vue-property-decorator';
interface Ibreadscumb {
    name?: string;
    active?: boolean;
    path?: string;
}
@Component({})
export default class ZhCrumbs extends Vue {
    @Prop() breadcrumbs: any;
    private showBreadscrumbs: Array<Ibreadscumb>  = this.breadcrumbs;

    created() {
        this.showBreadscrumbs = this.breadcrumbs;
    }
    routerChange(item: Ibreadscumb) {
        this.$emit('router-change', item);
    }
}
